body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.none {
  display: none;
}
.block {
  display: block;
}
.containerPrint {
  font-size: 1.5rem;
}
ul {
  margin: 200px 0 50px 0;
  font-size: 1.7rem;
}
hr {
  margin-top: 150px;
  border: 1px solid black;
}
.nazwiska {
  margin-top: 800px;
  position: absolute;
  bottom: 0;
  display: none;
  /* margin: auto; */
}
.text1 {
  padding: 100px 0 0 0;
  font-size: 1.6rem;
}
@media print {
  div.print {
    position: absolute;
    height: 100px;
    width: 1100px;
    font-size: 1.4rem;
    bottom: 2px;
    /* left: 0; */
  }
  .print {
    display: block;
    left: 50%;
    transform: translateX(-50%);
  }
  .listName {
    list-style-type: none;
  }
  .no-print,
  .no-print * {
    display: none !important;
  }
}
